import { TimeInMs } from 'constants/time';
import { CurrentUserType, PointType, WeeklySlotType } from 'types/common';
import { TeacherWeeklySlotsType, WeekDayType } from 'types/scheduleType';
import { addZero } from './helpers';

export const getLocalTimeOffsetByDate = (
  sourceDate: Date | string,
  user: CurrentUserType,
): number => {
  const offsetInfo = user.timezone_offset_info || {};
  let offset = user.timezone_offset || user.timezone_local_offset;

  const date = new Date(sourceDate).getTime();

  if (offsetInfo.next && date > offsetInfo.next.start_interval_timestamp) {
    offset = offsetInfo.next.offset_in_minutes;
  }

  if (offsetInfo.current && offsetInfo.next && date < offsetInfo.next.start_interval_timestamp) {
    offset = offsetInfo.current.offset_in_minutes;
  }

  if (
    offsetInfo.previous
    && offsetInfo.current
    && date < offsetInfo.current.start_interval_timestamp) {
    offset = offsetInfo.previous.offset_in_minutes;
  }

  if (
    offsetInfo.previous
    && offsetInfo.current
    && date < offsetInfo.previous.start_interval_timestamp) {
    offset = offsetInfo.current.offset_in_minutes;
  }

  return offset;
};

export const secToHHMM = (val = 0, defaultVal?: string): string => {
  if (val <= 0) {
    return defaultVal || '--:--';
  }

  const h = Math.floor(val / 60 / 60);
  const m = Math.floor((val - h * 60 * 60) / 60);
  // const s = val - h * 60 * 60 - m * 60;

  return `${addZero(h)}:${addZero(m)}`;
};

export const getTimePoints = (offset = 0, nightTeacher: boolean): PointType[] => {
  const points: PointType[] = [];
  const points2: PointType[] = [];
  const midnightPoint = 24 * 60 * 60;
  let sec: number = (4 * 60 + offset) * 60;
  let max = (21 * 60 + 30 + offset) * 60;
  const step = 30 * 60;

  if (nightTeacher) {
    sec = 0;
    max = 23.5 * 60 * 60;
  } else {
    sec = sec < 0 ? (midnightPoint + sec) : sec;
    max = max > midnightPoint ? (max - midnightPoint) : max;
  }

  if (sec > max) {
    while (sec < midnightPoint) {
      points2.push({
        timestamp: sec * 1000,
        str: secToHHMM(sec),
      });
      sec += step;
    }
    sec = 0;
  }

  while (sec <= max) {
    points.push({
      timestamp: sec * 1000,
      str: secToHHMM(sec, '00:00'),
    });
    sec += step;
  }

  return points2 && points2.length > 0 ? points.concat(points2) : points;
};

export const getWeekDays = (now = new Date(), offset = 0): WeekDayType[] => {
  const currentDate = new Date(Date.UTC(now.getUTCFullYear(), now.getUTCMonth(), now.getUTCDate()));
  const firstDay = new Date(Date.UTC(
    currentDate.getUTCFullYear(),
    currentDate.getUTCMonth(),
    currentDate.getUTCDate() - currentDate.getUTCDay() + 1,
  ));

  const days: WeekDayType[] = [];

  for (let i = 0; i < 7; i += 1) {
    const date = new Date(
      Date.UTC(
        firstDay.getUTCFullYear(),
        firstDay.getUTCMonth(),
        firstDay.getUTCDate() + i,
      ) - offset * TimeInMs.minute,
    );

    days.push({
      index: i,
      date,
      timestamp: date.getTime(),
      month: date.getUTCMonth(),
      isoweekday: date.getUTCDay() + 1,
    });
  }

  return days;
};

export const getTeacherWeeklySlots = (
  slots: WeeklySlotType[],
  offset: number,
): TeacherWeeklySlotsType | null => {
  if (!slots.length) {
    return null;
  }

  const teacherSlots: TeacherWeeklySlotsType = {};

  for (const slot of slots) {
    const time = slot.start_time;
    const { isoweekday } = slot;
    const [h, m] = time.split(':');
    const sec = (+h * 60 + offset) * 60 + +m * 60;

    teacherSlots[isoweekday] = teacherSlots[isoweekday] || {};
    teacherSlots[isoweekday][sec * 1000] = true;
  }

  return teacherSlots;
};

export const getHotTimePoints = (offset: number): { [index: string]: boolean } => {
  const points = {};
  const midnightPoint = 24 * 60 * 60;
  let sec = (14 * 60 + offset) * 60 + 30 * 60;
  let max = (17 * 60 + offset) * 60 + 30 * 60;
  const step = 30 * 60;

  sec = sec < 0 ? (midnightPoint + sec) : sec;
  max = max > midnightPoint ? (max - midnightPoint) : max;

  if (sec > max) {
    while (sec < midnightPoint) {
      points[sec * 1000] = true;
      sec += step;
    }
    sec = 0;
  }

  while (sec <= max) {
    points[sec * 1000] = true;
    sec += step;
  }

  return points;
};

export const getHalfPriorityTimePoints = (
  offset: number,
  start: number,
  end: number,
): { [index: string]: boolean } => {
  const points = {};
  const midnightPoint = 24 * 60 * 60;
  let sec = (start * 60 + offset) * 60 + 30 * 60;
  let max = (end * 60 + offset) * 60 + 30 * 60;
  const step = 30 * 60;

  sec = sec < 0 ? (midnightPoint + sec) : sec;
  max = max > midnightPoint ? (max - midnightPoint) : max;

  if (sec > max) {
    while (sec < midnightPoint) {
      points[sec * 1000] = true;
      sec += step;
    }
    sec = 0;
  }

  while (sec <= max) {
    points[sec * 1000] = true;
    sec += step;
  }

  return points;
};
