import { AppStateType } from 'types';
import { ScheduleState } from 'types/scheduleType';

export const getLibraryInfoLoading = (state: AppStateType): boolean => (
  state.schedule.weeklySlotsLoading
  || state.schedule.classesLoading
);

export const getScheduleInfo = (state: AppStateType): ScheduleState => state.schedule;
